.container {
	margin: 0 auto;
	max-width: 1920px;
	padding: 0 5%;
	@include rmin(1400) {
		padding: 0 8%; }
	&--h {
		height: 100%; }
	&-inner {
		padding: 0 13.65%;
		@include rmin(1400) {
			padding: 0 16.25%; } } }

.row {
	display: flex;
	margin: 0 -15px;
	flex-wrap: wrap; }

.col-1-3 {
	flex: 0 0 33.33333%;
	padding: 0 15px;
	max-width: 33.33333%;
	&_md {
		@include rmin(992) {
			flex: 0 0 33.33333%;
			padding: 0 15px;
			max-width: 33.33333%; } }
	&_lg {
		@include rmin(1200) {
			flex: 0 0 33.33333%;
			padding: 0 15px;
			max-width: 33.33333%; } } }

.col-2-3 {
	flex: 0 0 66.66667%;
	padding: 0 15px;
	max-width: 66.66667%;
	&_md {
		@include rmin(992) {
			flex: 0 0 66.66667%;
			padding: 0 15px;
			max-width: 66.66667%; } }
	&_lg {
		@include rmin(1200) {
			flex: 0 0 66.66667%;
			padding: 0 15px;
			max-width: 66.66667%; } } }

.col-1-4 {
	flex: 0 0 25%;
	padding: 0 15px;
	max-width: 25%;
	&_md {
		@include rmin(992) {
			flex: 0 0 25%;
			padding: 0 15px;
			max-width: 25%; } }
	&_lg {
		@include rmin(1200) {
			flex: 0 0 25%;
			padding: 0 15px;
			max-width: 25%; } } }

.col-1-2 {
	flex: 0 0 50%;
	padding: 0 15px;
	max-width: 50%;
	&_md {
		@include rmin(992) {
			flex: 0 0 50%;
			padding: 0 15px;
			max-width: 50%; } }
	&_lg {
		@include rmin(1200) {
			flex: 0 0 50%;
			padding: 0 15px;
			max-width: 50%; } } }

.col-1-1 {
	flex: 0 0 100%;
	padding: 0 15px;
	max-width: 100%;
	&_md {
		@include rmin(992) {
			flex: 0 0 100%;
			padding: 0 15px;
			max-width: 100%; } }
	&_lg {
		@include rmin(1200) {
			flex: 0 0 100%;
			padding: 0 15px;
			max-width: 100%; } } }
