@include font('affect', affect-webfont);
@include font('affect', affect_light-webfont, 300);
@include font('affect', affect_bold-webfont, 700);

html {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

body {
	background: #FFF;
	font-family: "Open Sans",sans-serif;
	font-weight: 300;
	font-size: 16px;
	line-height: 1.4;
	color: $color_t;
	@include rmin(1400) {
		font-size: 18px;
		line-height: 1.76; } }

p {
	margin-bottom: 15px;
	@include rmin(1400) {
		margin-bottom: 25px; } }

blockquote, .blockquote {
	position: relative;
	font-size: 20px;
	font-weight: 400;
	line-height: 1.55;
	border-left: 3px solid $color_secondary;
	padding-left: 45px;
	margin-bottom: 60px;
	@include rmin(1400) {
		font-size: 22px; } }

cite {
	position: absolute;
	bottom: -60px;
	right: 0;
	display: block;
	text-align: right;
	font-size: 14px;
	font-style: normal;
	font-weight: 300;
	@include rmin(1400) {
		font-size: 16px; } }


h1 {
	font-family: $font_secondary;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 40px;
	line-height: 1.2; }
h2 {
	font-family: $font_secondary;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 38px;
	line-height: 1.2; }
h3 {
	font-family: $font_secondary;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 30px;
	line-height: 1.2; }

.img-responsive {
	max-width: 100%;
	height: auto;
	border: none; }

.link {
	font-size: 18px;
	line-height: 1.2;
	text-decoration: none;
	color: $color_secondary; }

.modal-container {
	max-width: 1920px;
	margin: 0 5%; }

.i-prop {
	font-style: normal;
	font-weight: normal; }

.out {
	opacity: 0;
	transition: opacity .8s linear;
	transition-delay: .6s;
	.fadein & {
		opacity: 1; } }
